open Dec
open IntHash
open PrimInt63

type __ = Obj.t
let __ = let rec f _ = Obj.repr f in Obj.repr f

type 'm coq_IntMap = { empty : (__ -> 'm);
                       add : (__ -> Uint63.t -> __ -> 'm -> 'm);
                       lookup : (__ -> Uint63.t -> 'm -> __ option);
                       size : (__ -> 'm -> int) }

(** val lookup : 'a1 coq_IntMap -> Uint63.t -> 'a1 -> 'a2 option **)

let lookup intMap x x0 =
  Obj.magic intMap.lookup __ x x0

(** val hash_lookup :
    'a1 coq_IntMap -> 'a2 coq_IntHash -> 'a2 -> 'a1 -> 'a3 option **)

let hash_lookup h h0 x =
  lookup h (h0 x)

(** val int_Discrete : Uint63.t coq_Discrete **)

let int_Discrete x y =
  let b = eqb x y in if b then true else false

(** val in_dec : 'a1 coq_Discrete -> 'a1 -> 'a1 list -> bool **)

let rec in_dec h x = function
| [] -> false
| y :: l -> let d = h y x in if d then true else in_dec h x l

(** val in_decb : 'a1 coq_Discrete -> 'a1 -> 'a1 list -> bool **)

let in_decb h x xs =
  if in_dec h x xs then true else false

(** val coq_Hash_disc : 'a1 coq_IntHash -> 'a1 coq_Discrete **)

let coq_Hash_disc h x y =
  int_Discrete (h x) (h y)
