open IntMap

type __ = Obj.t

type 'v coq_OM = 'v M.t

(** val empty : 'a1 coq_OM **)

let empty = M.empty

(** val add : Uint63.t -> 'a1 -> 'a1 coq_OM -> 'a1 coq_OM **)

let add = M.insert

(** val lookup : Uint63.t -> 'a1 coq_OM -> 'a1 option **)

let lookup = M.lookup

(** val size : 'a1 coq_OM -> int **)

let size = M.size

(** val coq_OMap : __ coq_OM coq_IntMap **)

let coq_OMap =
  { IntMap.empty = (fun _ -> empty); IntMap.add = (fun _ -> add);
    IntMap.lookup = (fun _ -> lookup); IntMap.size = (fun _ -> size) }
