open Game
open IntHash
open IntMap
open OMap
open Player
open Specif
open Strategy

type __ = Obj.t
let __ = let rec f _ = Obj.repr f in Obj.repr f

type coq_OCamlTablebase = { tb_whites : (coq_Player * int) coq_OM;
                            tb_blacks : (coq_Player * int) coq_OM }

(** val query_TB :
    coq_Game -> coq_GameState coq_IntHash -> coq_OCamlTablebase ->
    coq_GameState -> (coq_Player * int) option **)

let query_TB g h tb s =
  match g.to_play s with
  | White -> hash_lookup (Obj.magic coq_OMap) h s tb.tb_whites
  | Black -> hash_lookup (Obj.magic coq_OMap) h s tb.tb_blacks

(** val p_leb :
    coq_Player -> (coq_Player * int) option -> (coq_Player * int) option ->
    bool **)

let p_leb pl r1 r2 =
  match pl with
  | White ->
    (match r1 with
     | Some p ->
       let (p0, m) = p in
       (match p0 with
        | White ->
          (match r2 with
           | Some p1 ->
             let (p2, n) = p1 in
             (match p2 with
              | White -> (<=) n m
              | Black -> false)
           | None -> false)
        | Black ->
          (match r2 with
           | Some p1 ->
             let (p2, n) = p1 in
             (match p2 with
              | White -> true
              | Black -> (<=) m n)
           | None -> true))
     | None ->
       (match r2 with
        | Some p ->
          let (p0, _) = p in (match p0 with
                              | White -> true
                              | Black -> false)
        | None -> true))
  | Black ->
    (match r1 with
     | Some p ->
       let (p0, m) = p in
       (match p0 with
        | White ->
          (match r2 with
           | Some p1 ->
             let (p2, n) = p1 in
             (match p2 with
              | White -> (<=) m n
              | Black -> true)
           | None -> true)
        | Black ->
          (match r2 with
           | Some p1 ->
             let (p2, n) = p1 in
             (match p2 with
              | White -> false
              | Black -> (<=) n m)
           | None -> false))
     | None ->
       (match r2 with
        | Some p ->
          let (p0, _) = p in (match p0 with
                              | White -> false
                              | Black -> true)
        | None -> true))

(** val max_by : ('a1 -> 'a1 -> bool) -> 'a1 list -> 'a1 option **)

let rec max_by x_leb = function
| [] -> None
| x :: xs' ->
  (match max_by x_leb xs' with
   | Some y -> if x_leb x y then Some y else Some x
   | None -> Some x)

(** val max_by_ne_Some :
    ('a1 -> 'a1 -> bool) -> 'a1 list -> ('a1, __) sigT **)

let max_by_ne_Some x_leb = function
| [] -> assert false (* absurd case *)
| x :: l ->
  let o = max_by x_leb l in
  (match o with
   | Some x0 ->
     let b = x_leb x x0 in
     if b then Coq_existT (x0, __) else Coq_existT (x, __)
   | None -> Coq_existT (x, __))

(** val max_by_ne : ('a1 -> 'a1 -> bool) -> 'a1 list -> 'a1 **)

let max_by_ne x_leb xs =
  projT1 (max_by_ne_Some x_leb xs)

(** val tb_strat :
    coq_Game -> coq_GameState -> coq_Player -> 'a1 coq_IntMap ->
    coq_GameState coq_IntHash -> coq_OCamlTablebase -> strategy **)

let rec tb_strat g s pl h h0 tb =
  let o = g.atomic_res s in
  (match o with
   | Some r -> lazy (Coq_atom_strategy (s, r))
   | None ->
     let s0 = player_id_or_opp_r_t (g.to_play s) pl in
     if s0
     then let m =
            max_by_ne (fun m1 m2 ->
              p_leb pl (query_TB g h0 tb (g.exec_move s m1))
                (query_TB g h0 tb (g.exec_move s m2))) (g.enum_moves s)
          in
          lazy (Coq_eloise_strategy (s, m,
          (tb_strat g (g.exec_move s m) pl h h0 tb)))
     else lazy (Coq_abelard_strategy (s, (fun m ->
            tb_strat g (g.exec_move s m) pl h h0 tb))))
