(** val show_RWVert : coq_RWVert -> string **)

let show_RWVert = function
| Center -> strC
| SpokeVert (s, l) ->
  (match s with
   | S1 -> (match l with
            | Mid -> str1M
            | L -> str1L
            | R -> str1R)
   | S2 -> (match l with
            | Mid -> str2M
            | L -> str2L
            | R -> str2R)
   | S3 -> (match l with
            | Mid -> str3M
            | L -> str3L
            | R -> str3R)
   | S4 -> (match l with
            | Mid -> str4M
            | L -> str4L
            | R -> str4R)
   | S5 -> (match l with
            | Mid -> str5M
            | L -> str5L
            | R -> str5R)
   | S6 -> (match l with
            | Mid -> str6M
            | L -> str6L
            | R -> str6R)
   | S7 -> (match l with
            | Mid -> str7M
            | L -> str7L
            | R -> str7R)
   | S8 -> (match l with
            | Mid -> str8M
            | L -> str8L
            | R -> str8R))

(** val coq_Show_RWVert : coq_RWVert coq_Show **)

let coq_Show_RWVert =
  show_RWVert

(** val print_RW_move : coq_BG_State -> coq_BG_Move -> string **)

let print_RW_move _ = function
| BearMove b -> (^) "B" (Obj.magic coq_Show_RWVert b)
| HunterMove h ->
  let { h_orig = h_orig0; h_dest = h_dest0 } = h in
  (^) "H"
    ((^) (Obj.magic coq_Show_RWVert h_orig0)
      (Obj.magic coq_Show_RWVert h_dest0))

(** val coq_RW_exec_move : coq_BG_State -> coq_BG_Move -> coq_BG_State **)

let coq_RW_exec_move s =
  BearGame.exec_move coq_RomanWheel s

(** val query_RW_TB :
    coq_OCamlTablebase -> coq_BG_State -> (coq_Player * int) option **)

let query_RW_TB =
  Obj.magic query_TB (coq_BearGame coq_RomanWheel) coq_IntHash_RW

(** val rw_tb_strat :
    coq_Player -> coq_GameState -> coq_OCamlTablebase -> strategy **)

let rw_tb_strat pl s tb =
  tb_strat (coq_BearGame coq_RomanWheel) s pl coq_OMap coq_IntHash_RW tb
