open Datatypes
open Dec
open Game
open Graph
open IntMap
open List0
open Player
open Sort
open TB

type __ = Obj.t
let __ = let rec f _ = Obj.repr f in Obj.repr f

(** val coq_NoDup_dec : 'a1 coq_Discrete -> 'a1 list -> bool **)

let rec coq_NoDup_dec h = function
| [] -> true
| y :: l -> let s = in_dec h y l in if s then false else coq_NoDup_dec h l

type coq_BG_State = { to_play : coq_Player; bear : coq_Vert;
                      hunters : coq_Vert list }

type coq_BearMv =
  coq_Vert
  (* singleton inductive, whose constructor was Build_BearMv *)

type coq_HunterMv = { h_orig : coq_Vert; h_dest : coq_Vert }

type coq_BG_Move =
| BearMove of coq_BearMv
| HunterMove of coq_HunterMv

(** val create_Move :
    coq_Graph -> coq_BG_State -> coq_Vert -> coq_Vert -> coq_BG_Move option **)

let create_Move g s v v' =
  let p = s.to_play in
  (match p with
   | White ->
     let s0 = in_dec (coq_Graph_Vert_disc g) v s.hunters in
     if s0
     then let s1 = in_dec (coq_Graph_Vert_disc g) v' (g.successors v) in
          if s1
          then let d = coq_Graph_Vert_disc g v' s.bear in
               if d
               then None
               else let s2 = in_dec (coq_Graph_Vert_disc g) v' s.hunters in
                    if s2
                    then let d0 = coq_Graph_Vert_disc g v' v in
                         if d0
                         then Some (HunterMove { h_orig = v; h_dest = v' })
                         else None
                    else Some (HunterMove { h_orig = v; h_dest = v' })
          else None
     else None
   | Black ->
     let d = coq_Graph_Vert_disc g v s.bear in
     if d
     then let s0 = in_dec (coq_Graph_Vert_disc g) v' (g.successors v) in
          if s0
          then let s1 = in_dec (coq_Graph_Vert_disc g) v' s.hunters in
               if s1 then None else Some (BearMove v')
          else None
     else None)

(** val is_hunter : coq_Graph -> coq_BG_State -> coq_Vert -> bool **)

let is_hunter g s v =
  if in_dec (coq_Graph_Vert_disc g) v s.hunters then true else false

(** val is_bear : coq_Graph -> coq_BG_State -> coq_Vert -> bool **)

let is_bear g s v =
  if coq_Graph_Vert_disc g v s.bear then true else false

(** val exec_move :
    coq_Graph -> coq_BG_State -> coq_BG_Move -> coq_BG_State **)

let exec_move g s = function
| BearMove b -> { to_play = White; bear = b; hunters = s.hunters }
| HunterMove h ->
  { to_play = Black; bear = s.bear; hunters =
    (insertion_sort
      (subcount_Ord (coq_Graph_Vert_disc g) (coq_Graph_Vert_enum g))
      (h.h_dest :: (remove (coq_Graph_Vert_disc g) h.h_orig s.hunters))) }

(** val pf_map : 'a1 list -> ('a1 -> __ -> 'a2) -> 'a2 list **)

let rec pf_map xs f =
  match xs with
  | [] -> []
  | x :: l -> (f x __) :: (pf_map l (fun x' _ -> f x' __))

(** val eqb : 'a1 coq_Discrete -> 'a1 -> 'a1 -> bool **)

let eqb h x x' =
  if h x x' then true else false

(** val enum_moves : coq_Graph -> coq_BG_State -> coq_BG_Move list **)

let enum_moves g s =
  let p = s.to_play in
  (match p with
   | White ->
     concat
       (pf_map s.hunters (fun h _ ->
         pf_map
           (filter (fun v ->
             (&&) (negb (eqb (coq_Graph_Vert_disc g) v s.bear))
               ((||) (negb (in_decb (coq_Graph_Vert_disc g) v s.hunters))
                 (eqb (coq_Graph_Vert_disc g) v h))) (g.successors h))
           (fun v _ -> HunterMove { h_orig = h; h_dest = v })))
   | Black ->
     pf_map
       (filter (fun v -> negb (in_decb (coq_Graph_Vert_disc g) v s.hunters))
         (g.successors s.bear)) (fun v _ -> BearMove v))

(** val atomic_res : coq_Graph -> coq_BG_State -> coq_Result option **)

let atomic_res g s =
  match enum_moves g s with
  | [] ->
    (match s.to_play with
     | White -> Some Draw
     | Black -> Some (Win White))
  | _ :: _ -> None

(** val coq_BearGame : coq_Graph -> coq_Game **)

let coq_BearGame g =
  { Game.to_play = (Obj.magic (fun b -> b.to_play)); Game.exec_move =
    (Obj.magic exec_move g); Game.atomic_res = (Obj.magic atomic_res g);
    Game.enum_moves = (Obj.magic enum_moves g) }
