open BearGame
open Game
open Graph
open IntHash
open IntMap
open List0
open Player
open PrimInt63

type coq_Spoke =
| S1
| S2
| S3
| S4
| S5
| S6
| S7
| S8

(** val clockwise : coq_Spoke -> coq_Spoke **)

let clockwise = function
| S1 -> S2
| S2 -> S3
| S3 -> S4
| S4 -> S5
| S5 -> S6
| S6 -> S7
| S7 -> S8
| S8 -> S1

(** val c_clockwise : coq_Spoke -> coq_Spoke **)

let c_clockwise = function
| S1 -> S8
| S2 -> S1
| S3 -> S2
| S4 -> S3
| S5 -> S4
| S6 -> S5
| S7 -> S6
| S8 -> S7

(** val list_spokes : coq_Spoke list **)

let list_spokes =
  S1 :: (S2 :: (S3 :: (S4 :: (S5 :: (S6 :: (S7 :: (S8 :: [])))))))

type coq_SpokeLoc =
| Mid
| L
| R

(** val list_locs : coq_SpokeLoc list **)

let list_locs =
  Mid :: (L :: (R :: []))

type coq_RWVert =
| Center
| SpokeVert of coq_Spoke * coq_SpokeLoc

(** val hash_RWVert : coq_RWVert -> Uint63.t **)

let hash_RWVert = function
| Center -> (Uint63.of_int (0))
| SpokeVert (s, l) ->
  (match s with
   | S1 ->
     (match l with
      | Mid -> (Uint63.of_int (2))
      | L -> (Uint63.of_int (1))
      | R -> (Uint63.of_int (3)))
   | S2 ->
     (match l with
      | Mid -> (Uint63.of_int (5))
      | L -> (Uint63.of_int (4))
      | R -> (Uint63.of_int (6)))
   | S3 ->
     (match l with
      | Mid -> (Uint63.of_int (8))
      | L -> (Uint63.of_int (7))
      | R -> (Uint63.of_int (9)))
   | S4 ->
     (match l with
      | Mid -> (Uint63.of_int (11))
      | L -> (Uint63.of_int (10))
      | R -> (Uint63.of_int (12)))
   | S5 ->
     (match l with
      | Mid -> (Uint63.of_int (14))
      | L -> (Uint63.of_int (13))
      | R -> (Uint63.of_int (15)))
   | S6 ->
     (match l with
      | Mid -> (Uint63.of_int (17))
      | L -> (Uint63.of_int (16))
      | R -> (Uint63.of_int (18)))
   | S7 ->
     (match l with
      | Mid -> (Uint63.of_int (20))
      | L -> (Uint63.of_int (19))
      | R -> (Uint63.of_int (21)))
   | S8 ->
     (match l with
      | Mid -> (Uint63.of_int (23))
      | L -> (Uint63.of_int (22))
      | R -> (Uint63.of_int (24))))

(** val coq_IntHash_RWVert : coq_RWVert coq_IntHash **)

let coq_IntHash_RWVert =
  hash_RWVert

(** val coq_RomanWheel : coq_Graph **)

let coq_RomanWheel =
  { coq_Vert_disc = (coq_Hash_disc (Obj.magic coq_IntHash_RWVert));
    coq_Vert_enum =
    ((Obj.magic Center) :: (concat
                             (map (fun s ->
                               map (Obj.magic (fun x -> SpokeVert (s, x)))
                                 list_locs) list_spokes))); successors =
    (fun v ->
    match Obj.magic v with
    | Center -> map (fun s -> Obj.magic (SpokeVert (s, Mid))) list_spokes
    | SpokeVert (s, l) ->
      (match l with
       | Mid ->
         (Obj.magic Center) :: ((Obj.magic (SpokeVert (s, L))) :: ((Obj.magic
                                                                    (SpokeVert
                                                                    (s, R))) :: []))
       | L ->
         (Obj.magic (SpokeVert (s, R))) :: ((Obj.magic (SpokeVert (s, Mid))) :: (
           (Obj.magic (SpokeVert ((c_clockwise s), R))) :: []))
       | R ->
         (Obj.magic (SpokeVert (s, L))) :: ((Obj.magic (SpokeVert (s, Mid))) :: (
           (Obj.magic (SpokeVert ((clockwise s), L))) :: [])))) }

(** val coq_IntHash_RWV : coq_Vert coq_IntHash **)

let coq_IntHash_RWV =
  Obj.magic coq_IntHash_RWVert

(** val add_vert : coq_Vert -> Uint63.t -> Uint63.t **)

let add_vert v i =
  coq_lor (coq_IntHash_RWV v) (coq_lsl i (Uint63.of_int (5)))

(** val add_verts : coq_Vert list -> Uint63.t -> Uint63.t **)

let add_verts vs i =
  fold_right add_vert i vs

(** val add_player : coq_Player -> Uint63.t -> Uint63.t **)

let add_player p i =
  match p with
  | White -> coq_lsl i (Uint63.of_int (1))
  | Black -> coq_lor (Uint63.of_int (1)) (coq_lsl i (Uint63.of_int (1)))

(** val hash_RW_State : coq_BG_State -> Uint63.t **)

let hash_RW_State s =
  let { BearGame.to_play = p; bear = b; hunters = hs } = s in
  add_player p (add_vert b (add_verts hs (Uint63.of_int (0))))

(** val coq_IntHash_RW : coq_GameState coq_IntHash **)

let coq_IntHash_RW =
  Obj.magic hash_RW_State
