open Compare_dec
open Dec
open Sort
open Specif

type __ = Obj.t
let __ = let rec f _ = Obj.repr f in Obj.repr f

type 'x coq_Enum =
  'x list
  (* singleton inductive, whose constructor was Build_Enum *)

type coq_Graph = { coq_Vert_disc : __ coq_Discrete;
                   coq_Vert_enum : __ coq_Enum; successors : (__ -> __ list) }

type coq_Vert = __

(** val coq_Graph_Vert_disc : coq_Graph -> coq_Vert coq_Discrete **)

let coq_Graph_Vert_disc g =
  g.coq_Vert_disc

(** val coq_Graph_Vert_enum : coq_Graph -> coq_Vert coq_Enum **)

let coq_Graph_Vert_enum g =
  g.coq_Vert_enum

(** val first_index_aux_In :
    'a1 coq_Discrete -> 'a1 list -> int -> 'a1 -> (int, __) sigT **)

let rec first_index_aux_In h xs n x =
  match xs with
  | [] -> assert false (* absurd case *)
  | y :: l ->
    let d = h x y in
    if d
    then Coq_existT (n, __)
    else first_index_aux_In h l (Stdlib.Int.succ n) x

(** val subcount_Ord : 'a1 coq_Discrete -> 'a1 coq_Enum -> 'a1 coq_Ord **)

let subcount_Ord h h0 x y =
  let s = first_index_aux_In h h0 0 x in
  let Coq_existT (x0, _) = s in
  let s0 = first_index_aux_In h h0 0 y in
  let Coq_existT (x1, _) = s0 in le_le_S_dec x0 x1
